#F5F5F5;\n        color: #616161;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n}\n.view-btn[data-v-9703db96] {\n        background: none;\n        border:none;\n        color: #2C5A86;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n}\n.items-per-page[data-v-9703db96] {\n        background: #FFFFFF;\n        border: 1px solid #BCBCBC;\n        border-radius: 4px;\n        width: 84px;\n        padding: 7px 12px;\n        margin-left: 12px\n}\n.page-number[data-v-9703db96] {\n        padding: 8px 12px;\n        color: #616161;\n        font-size: 14px;\n        line-height: 16px;\n        background: none;\n        border: none\n}\n.page-number.active[data-v-9703db96] {\n        background: #151233;\n        border-radius: 4px;\n        color: #FFFFFF;\n}\n.change-page-number[data-v-9703db96] {\n        border: none;\n        background: none;\n}\n.change-page-number i[data-v-9703db96] {\n        color: #151233; \n        font-size: 20px; \n        line-height: 20px;\n}\n.remove-btn[data-v-9703db96] {\n        background: none;\n        border:none;\n        color: #FF6B6B;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n}\n.create-tag[data-v-9703db96] {\n        background: #2C5A86; \n        border-radius: 4px; \n        border: none; \n        padding: 8px 16px;\n        font-size: 14px; \n        font-weight: 500; \n        color: #FFFFFF;\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n        height: 32px;\n        margin-left: 20px;\n}\n.tag-name[data-v-9703db96] {\n        padding: 8px;\n        background: #D0D1E9;\n        display: flex;\n        justify-content: center;\n        border-radius: 4px;\n        white-space: nowrap;\n        color: #2F3160;\n        font-weight: 500;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/backoffice/userRequests/userRequestsList.vue"],"names":[],"mappings":";AAkJI;QACI,qBAAqB;AACzB;AACA;QACI,gBAAgB;QAChB,eAAe;QACf,iBAAiB;QACjB,cAAc;QACd,mBAAmB;AACvB;AACA;QACI,cAAc;QACd,aAAa;QACb,mBAAmB;AACvB;AACA;QACI,uBAAuB;QACvB,oCAAoC;QACpC,6BAA6B;QAC7B,YAAY;QACZ,aAAa;QACb,mBAAmB;AACvB;AACA;QACI,uBAAuB;QACvB,YAAY;AAChB;AACA;QACI,aAAa;AACjB;AACA;QACI,cAAc;QACd,eAAe;QACf,gBAAgB;AACpB;AACA;QACI,mBAAmB;QACnB,yBAAyB;QACzB,8CAA8C;QAC9C,mBAAmB;QACnB,WAAW;AACf;AACA;QACI,iCAAiC;AACrC;AACA;QACI,yBAAyB;AAC7B;AACA;QACI,gCAAgC;QAChC;AACJ;AACA;QACI,gCAAgC;QAChC,kBAAkB;QAClB,cAAc;QACd,eAAe;QACf,iBAAiB;QACjB,uBAAuB;QACvB,gBAAgB;QAChB,mBAAmB;QACnB,eAAe;AACnB;AACA;QACI,gCAAgC;QAChC,kBAAkB;QAClB,cAAc;QACd,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,mBAAmB;QACnB,cAAc;QACd,gBAAgB;QAChB,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,gBAAgB;QAChB,WAAW;QACX,cAAc;QACd,gBAAgB;QAChB,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,mBAAmB;QACnB,yBAAyB;QACzB,kBAAkB;QAClB,WAAW;QACX,iBAAiB;QACjB;AACJ;AACA;QACI,iBAAiB;QACjB,cAAc;QACd,eAAe;QACf,iBAAiB;QACjB,gBAAgB;QAChB;AACJ;AACA;QACI,mBAAmB;QACnB,kBAAkB;QAClB,cAAc;AAClB;AACA;QACI,YAAY;QACZ,gBAAgB;AACpB;AACA;QACI,cAAc;QACd,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,gBAAgB;QAChB,WAAW;QACX,cAAc;QACd,gBAAgB;QAChB,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,mBAAmB;QACnB,kBAAkB;QAClB,YAAY;QACZ,iBAAiB;QACjB,eAAe;QACf,gBAAgB;QAChB,cAAc;QACd,aAAa;QACb,mBAAmB;QACnB,mBAAmB;QACnB,YAAY;QACZ,iBAAiB;AACrB;AACA;QACI,YAAY;QACZ,mBAAmB;QACnB,aAAa;QACb,uBAAuB;QACvB,kBAAkB;QAClB,mBAAmB;QACnB,cAAc;QACd,gBAAgB;AACpB","sourcesContent":["<script setup lang=\"ts\">\n    import { computed, ref, watchEffect } from \"vue\";\n    import { IUserRequestExtended } from \"../../../../../domain/useCase/IBackofficeUseCase\";\n    import CustomSelect from \"../../customSelect.vue\"\n\n    interface IUserRequestListProps {\n        requests: Array<IUserRequestExtended>;\n    }\n\n    const props = defineProps<IUserRequestListProps>();\n    const requests = computed(() => props.requests);\n\n\n    const emit = defineEmits([\"refreshTable\"]);\n    const searchRequest = ref(\"\");\n    const currentPage = ref(1);\n    const paginatedData = ref<Array<IUserRequestExtended>>([]);\n    const selectedItemsPerPage = ref('10');\n    const itemsPerPageOptions = ref(['10', '20', '30', 'All']);\n\n    function viewOrganization(orgID: string) {\n        window.open(`/backoffice/organization/${orgID}`, \"_blank\");\n    }\n\n\n    function formatDate(date: Date) {\n        const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });\n        const formattedDate = formatter.format(new Date(date));\n        return formattedDate;\n    }\n    function getRequestList() {\n        return requests.value.filter((request: IUserRequestExtended) =>request.organizationAccountType != 'premium' && (request.details.companyName.toUpperCase().includes(searchRequest.value.toUpperCase()) || \n                                                                request.details.phoneNumber.toUpperCase().includes(searchRequest.value.toUpperCase()) ||\n                                                                request.userName!.toUpperCase().includes(searchRequest.value.toUpperCase()) || \n                                                                request.userLastname!.toUpperCase().includes(searchRequest.value.toUpperCase()) || \n                                                                request.userRole!.toUpperCase().includes(searchRequest.value.toUpperCase()) || \n                                                                request.organizationName!.toUpperCase().includes(searchRequest.value.toUpperCase())) );\n    }\n\n    const totalPages = computed(() => {\n        if(selectedItemsPerPage.value != 'All') {\n            return Math.ceil(getRequestList().length / parseInt(selectedItemsPerPage.value))\n        }\n        else {\n            return 1;\n        }\n    });\n\n    const displayedPageNumbers = computed(() => {\n        const range = 2; // Number of page numbers to display on each side of the current page\n        let startPage = Math.max(1, currentPage.value - range);\n        let endPage = Math.min(totalPages.value, currentPage.value + range);\n        if (endPage - startPage < 2 * range) {\n        // Adjust page numbers to always show the same number of items\n        if (startPage === 1) {\n            endPage = Math.min(endPage + (2 * range - (endPage - startPage)), totalPages.value);\n        } else if (endPage === totalPages.value) {\n            startPage = Math.max(startPage - (2 * range - (endPage - startPage)), 1);\n        }\n        }\n        return Array.from({ length: endPage - startPage + 1 }, (_, index) => startPage + index);\n    });\n\n    watchEffect(() => {\n        if(selectedItemsPerPage.value != 'All') {\n            const startIndex = (currentPage.value - 1) * parseInt(selectedItemsPerPage.value);\n            const endIndex = startIndex + parseInt(selectedItemsPerPage.value);\n            const newData = getRequestList().slice(startIndex, endIndex);\n            paginatedData.value = newData;\n        }\n        else {\n            paginatedData.value = getRequestList();\n        }\n    });\n    function setCurrentPage(page: number) {\n        currentPage.value = page;\n    }\n    function onItemsPerPageSelect (itemCount: string) {\n        selectedItemsPerPage.value = itemCount;\n        setCurrentPage(1)\n    }\n    watchEffect(() => {\n        currentPage.value = 1;\n    });\n</script>\n\n<template>\n    <div class=\"page-title\">\n        <span>Premium Requests List ({{ getRequestList().length }})</span>\n    </div>\n    <div class=\"filter-area\">\n        <div class=\"search-in-table\">\n            <i class=\"mdi mdi-magnify\"></i>\n            <input class=\"search-input\" type=\"text\" placeholder=\"Search\" alt=\"search\" v-model=\"searchRequest\" @input=\"setCurrentPage(1)\"/>\n        </div>\n    </div>\n    <div style=\"padding-bottom: 50px;\">\n        <table class=\"tags-table\">\n            <tr class=\"thead\">\n                <td class=\"col-xl-1\">User Name</td>\n                <td class=\"col-xl-1\">User Last Name</td>\n                <td class=\"col-xl-1\">User Role</td>\n                <td class=\"col-xl-2\">Organization</td>\n                <th class=\"col-xl-2\">Company Name</th>\n                <th class=\"col-xl-1\">Org Dev Count</th>\n                <th class=\"col-xl-2\">Phone</th>\n                <th class=\"col-xl-2\">Date Created</th>\n            </tr>\n            <tr v-for=\"request in paginatedData\" :key=\"request.id\">\n                <td>{{ request.userName}}</td>\n                <td>{{ request.userLastname}}</td>\n                <td>{{ request.userRole}}</td>\n                <td @click=\"viewOrganization(request.details.sentOrganizationId)\" style=\"cursor: pointer;color: #268bc9;\">\n                    {{ request.organizationName}}<i class=\"mdi mdi-open-in-new\" style=\"margin-left: 5px;\"></i></td>\n                <td>{{ request.details.companyName }}</td>\n                <td>{{ request.details.orgDevCount }}</td>\n                <td>{{ request.details.phoneNumber }}</td>\n                <td>{{ formatDate(request.createDate) }}</td>\n                <!-- <td><div style=\"display: flex;\">\n                        <button class=\"remove-btn\" @click=\"deleteTag(tag.ID)\">DELETE</button>\n                        <button class=\"view-btn\" @click=\"viewTag(tag.ID)\">VIEW</button>       \n                    </div>\n                </td> -->\n            </tr>\n            <tr>\n                <td colspan=\"1\" style=\"display: flex; align-items: center;\">\n                    <span style=\"font-size: 14px; color: #616161\">Items per page:</span>\n                    <CustomSelect :options=\"itemsPerPageOptions\" @input=\"onItemsPerPageSelect\" class=\"items-per-page\" :selected-class=\"'item-count'\" :default=\"selectedItemsPerPage\"></CustomSelect>\n                </td>\n                <td colspan=\"9\">\n                    <div style=\"float: right; display: flex; align-items: center;\">\n                        <button class=\"change-page-number\" @click=\"currentPage--\" :disabled=\"currentPage === 1\">\n                            <i class=\"mdi mdi-chevron-left\" :style=\"{color: currentPage === 1 ? '#BCBCBC' : ''}\"></i>\n                        </button>\n                        <button v-for=\"pageNumber in displayedPageNumbers\" :key=\"pageNumber\" @click=\"setCurrentPage(pageNumber)\" :class=\"currentPage === pageNumber ? 'page-number active' : 'page-number'\"> {{ pageNumber }} </button>\n                        <button class=\"change-page-number\" @click=\"currentPage++\" :disabled=\"currentPage === totalPages\">\n                            <i class=\"mdi mdi-chevron-right\" :style=\"{color: currentPage === totalPages ? '#BCBCBC' : ''}\"></i>\n                        </button>\n                    </div>\n                </td>\n            </tr>\n        </table>\n    </div>\n</template>\n\n<style scoped>\n    * {\n        font-family: \"Roboto\";\n    }\n    .page-title {\n        font-weight: 500;\n        font-size: 24px;\n        line-height: 28px;\n        color: #151233;\n        margin-bottom: 20px;\n    }\n    .filter-area {\n        margin: 20px 0;\n        display: flex;\n        align-items: center;\n    }\n    .search-in-table {\n        background: transparent;\n        border: 1px solid #BCBCBC !important;\n        border-radius: 4px !important;\n        height: 32px;\n        display: flex;\n        align-items: center;\n    }\n    .search-input {\n        background: transparent;\n        border: none;\n    }\n    .search-input:focus {\n        outline: none;\n    }\n    .search-in-table i {\n        color: #BCBCBC;\n        font-size: 20px;\n        padding: 0px 4px;\n    }\n    .tags-table {\n        background: #FFFFFF;\n        border: 1px solid #E3E3E3;\n        box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n        border-radius: 10px;\n        width: 100%;\n    }\n    td:hover {\n        background-color: none !important;\n    }\n    table tr:hover:not(:last-child):not(:first-child) {\n        background-color: #EEEEEE;\n    }\n    .tags-table th {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 18px 36px\n    }\n    .tags-table tr:not(:last-child) td {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 12px 36px;\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n        text-overflow: ellipsis;\n        overflow: hidden;\n        white-space: nowrap;\n        cursor: default;\n    }\n    .tags-table tr:last-child td {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 12px 36px;\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .tags-table .thead {\n        background: #F5F5F5;\n        color: #616161;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .view-btn {\n        background: none;\n        border:none;\n        color: #2C5A86;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .items-per-page {\n        background: #FFFFFF;\n        border: 1px solid #BCBCBC;\n        border-radius: 4px;\n        width: 84px;\n        padding: 7px 12px;\n        margin-left: 12px\n    }\n    .page-number {\n        padding: 8px 12px;\n        color: #616161;\n        font-size: 14px;\n        line-height: 16px;\n        background: none;\n        border: none\n    }\n    .page-number.active {\n        background: #151233;\n        border-radius: 4px;\n        color: #FFFFFF;\n    }\n    .change-page-number {\n        border: none;\n        background: none;\n    }\n    .change-page-number i {\n        color: #151233; \n        font-size: 20px; \n        line-height: 20px;\n    }\n    .remove-btn {\n        background: none;\n        border:none;\n        color: #FF6B6B;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .create-tag {\n        background: #2C5A86; \n        border-radius: 4px; \n        border: none; \n        padding: 8px 16px;\n        font-size: 14px; \n        font-weight: 500; \n        color: #FFFFFF;\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n        height: 32px;\n        margin-left: 20px;\n    }\n    .tag-name {\n        padding: 8px;\n        background: #D0D1E9;\n        display: flex;\n        justify-content: center;\n        border-radius: 4px;\n        white-space: nowrap;\n        color: #2F3160;\n        font-weight: 500;\n    }\n</style>\n<style>\n    .items-per-page .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        margin-top: 8px;\n    }\n    .items-per-page .items .option {\n        color: #212121;\n        padding-left: 12px;\n        line-height: 32px;\n    }\n    .items-per-page .items .option:hover{\n        background-color: #EEEEEE;\n    }\n    .items-per-page i {\n        font-size: 16px;\n        color: #BCBCBC;\n    }\n</style>"],"sourceRoot":""}]);
